
import { Options, Vue } from "vue-class-component";
import { mapGetters, mapActions } from "vuex";

@Options({
  components: {},

  computed: {
    ...mapGetters({
      inited: "app/getInitState",
      status: "app/getStatus",
    }),
  },

  methods: {
    ...mapActions({
      init: "app/init",
    }),
  },

  async created() {
    this.init();
  },
})
export default class App extends Vue {}
